
a {
  text-decoration: none;
}
.is-collapsed {
	display: none;
}
#devtools {
  position: absolute;
  z-index: 50;
  background-color: white;
  border: 1px solid black;
  width: 35%;
  filter: drop-shadow(16px 16px 20px black);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s ease;
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
.child-view {
  position: absolute;
  transition: all .5s cubic-bezier(.55,0,.1,1);
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
  transform: translate(30px, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
  transform: translate(-30px, 0);
}
#devtoolsbtn {
  display: inherit;
}
.wrapper {
  display: flex;
  flex-flow: row wrap;
  font-weight: bold;
  text-align: center;
}
.wrapper > * {
  padding: 10px;
  flex: 1 1 100%;
}
.header {
  background: #FFAA5C;
}
.main {
  text-align: left;
  background: #3E99A6;
  flex: 1 0 auto;
  height: 75vh;
  overflow-y: scroll;
}
.footer {
  background: #73DB85;
  margin-top: auto;
}
.aside-1 {
  background: white;
}
.aside-2 {
  background: white;
}
@media all and (min-width: 600px) {
.aside { flex: 1 auto;
}
}
@media all and (min-width: 800px) {
.main    { flex: 3 0px;
}
.aside-1 { order: 1;
}
.main    { order: 2;
}
.aside-2 { order: 3;
}
.footer  { order: 4;
}
}
body {
  padding: 2em;
  background-image: url(/ld42static/img/metal.8ef632a.png);
  background-repeat: repeat;
}
.button {
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}
.button-green {background-color: #4CAF50;
} /* Green */
.button-blue {background-color: #008CBA;
} /* Blue */
.button-red {background-color: #f44336;
} /* Red */
.button-gray {background-color: #e7e7e7; color: black;
} /* Gray */
.button-black {background-color: #555555;
} /* Black */
.button-disabled {
  background-color: #555555;
  pointer-events: none;
}


